export default {
	onLoad() {
		uni.getStorage({
			key: 'openId',
			success: res=>{
				this.openId = res.data
				this.requestUrl()
				// 获取shopid 获取avatar&name
				this.onShopId()
			}
		})
		
	},
	data() {
		return {
			openId:'',
			isDefaultAvatar:true,
			avatarUrl:'../../../static/wxcomponentimg/avatar@2x.png',
			listTitle: [{
					title:'店铺详情'
			},{
					title:'修改密码'
			},{
					title:'我的收益'
			},{
					title:'在线客服'
			},{
					title:'推广二维码'
			}]
		}
	},
	methods:{
		onShopId(){
			uni.request({
				url:'https://www.finetwm.com/xmRepair/shopinfo/findIdByOpenid',
				method: 'GET',
				header:{
					"content-Type": "application/x-www-form-urlencoded"
				},
				data: {
					openid: this.openId
				},
				success: res=>{
					console.log(res)
				}
			})
		},
		requestUrl(){
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/shopInfo/getHeadPortraitAndName',
				method: 'GET',
				data: {
					openid:this.openId
				},
				success: res => {
					this.isDefaultAvatar = false
					console.log(res.data)
				}
			})
		},
		onDetail(index){
			console.log(index)
			if(index == 0){
				uni.navigateTo({
					url: '../business-detail/business-detail'
				})
			} else if(index == 1) {
				uni.navigateTo({
					url: '../business-key/business-key'
				})
			} else if(index == 2){
				uni.navigateTo({
					url: '../business-income/business-income'
				})
			} else if(index == 4){
				uni.navigateTo({
					url:'../business-code/business-code'
				})
			}
		},
		bindChooseImage(){
			uni.chooseImage({
				count:1,
				success:res=>{
					console.log(res.tempFilePaths[0])
					let tempFilePaths = res.tempFilePaths[0]
					this.avatarUrl = tempFilePaths
					this.isDefaultAvatar = false
					uni.request({
						url: 'https://www.finetwm.com/xmRepair/shopInfo/saveHeadPortrait',
						method: 'POST',
						data: {
							imgUrl:this.avatarUrl,
							id: 1
						},
						success: res => {
							console.log(res)
						},
						fail:err=>{
							console.log(err)
						}
					})
				}
			})
		}
	}
}

